/**
 * 集合图形绘制
 */
export default {
    /**
     * 绘制星
     * @param config
     * @return IPoint []
     */
    getDrawStartPath: function (config) {
        var startCount = config.startCount;
        var spend = 360 / startCount;
        var min = 90 - spend;
        var max = spend - min;
        var center = config.center, outRadius = config.outRadius, innerRadius = config.innerRadius;
        var path = [];
        for (var i = 0; i < startCount; i++) {
            path.push({
                x: Math.cos(((min + i * spend) / 180) * Math.PI) * outRadius + center.x,
                y: -Math.sin(((min + i * spend) / 180) * Math.PI) * outRadius + center.y,
            });
            path.push({
                x: Math.cos(((max + i * spend) / 180) * Math.PI) * innerRadius + center.x,
                y: -Math.sin(((max + i * spend) / 180) * Math.PI) * innerRadius + center.y,
            });
        }
        return path;
    },
};
